<template>
  <!-- 这是选择商品的那个弹窗 -->
  <div id="special-goods-name" class="layui-layer-wrap" style="display: none;">
    <!--左-->
    <div class="sbn-left">
      <div class="sbn-left-top">
        <img src="@/assets/img/ico-menu1.png" style="margin: -5px 2px -5px 5px"/>编码族管理
      </div>
      <div class="sbn-left-content">
        <ul id="file" class="filetree treeview">
          <li class="expandable lastExpandable">
            <div class="hitarea expandable-hitarea lastExpandable-hitarea"></div>
            <span class="folder 000">商品编码</span>
            <ul id="kgbmz_sbn"></ul>
          </li>
        </ul>
      </div>
    </div>
    <!--右-->
    <div class="sbn-right">
      <div class="sbn-right-top">
        <div class="right-box"></div>
        <div style="width: 150px;height: 23px;margin-top: 10px;float: left">
          <input style="width: 100%;height: 100%"/>
        </div>
      </div>
      <div class="sbn-right-content">
        <table id="sbn-right-content-table" cellpadding="0px" cellspacing="0px">
          <tbody>
          <tr>
            <th>编码</th>
            <th>名称</th>
            <th>简码</th>
            <th>商品税目</th>
            <th>税率</th>
            <th>规格型号</th>
            <th>计量单位</th>
            <th>含税价标志</th>
            <th>税收分类编码</th>
            <th>享受优惠政策</th>
            <th>税收分类名称</th>
            <th>优惠政策类型</th>
            <th>隐藏标志</th>
          </tr>
          <tr
            v-for="(item, i) in goods_form"
            :key="i"
            @click="select_goods(i)"
            @dblclick="dbSelectGoods(i)"
            :class="i===goodsNumber?'goods-active':''"
          >
            <td>{{item.CCP_bm_code}}</td>
            <td>{{item.CCP_sp_name}}</td>
            <td>{{item.CCP_Simple_code}}</td>
            <td>{{item.CCP_Commoditytax}}</td>
            <td>{{item.CCP_taxrate}}</td>
            <td>{{item.CCP_Specificationmodel}}</td>
            <td>{{item.CCP_UnitOfMeasurement}}</td>
            <!-- <td>{{item.CCP_UnitPrice}}</td> -->
            <td>{{item.CCP_TaxPriceMark}}</td>
            <td>{{item.CCP_TaxClassificationCode}}</td>
            <td></td>
            <td>{{item.CCP_NameOfTaxClassification}}</td>
            <td></td>
            <td>{{item.CCP_HiddenFlag}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'special-goods-name',
    data () {
      return {
        goodsDom: false,
        goodsNumber: false
      }
    },
    computed: {
      ...mapState({
        goods_form: (state) => { // 加载所有商品的信息
          return state.form.goods_form
        }
      })
    },
    methods: {
      pop_goods_name (num) {
        this.select_goods_num = num
        this.goodsDom = layer.open({
          type: 1,
          title: ['商品选择', 'color:#ffffff;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['1100px', '655px'],    //宽、高
          offset: '10px',
          fixed: false,
          content: $('#special-goods-name'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            // document.getElementById('special-buyer-name').style.display = 'none';
          }
        })
      },
      select_goods (i) { // 选择的时候高亮用的
        this.goodsNumber = i
      },
      dbSelectGoods (i) { // 选择商品的按钮
        let goodsData = this.goods_form[i]
        this.$emit('getDbGoods', goodsData)
        layer.close(this.goodsDom)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .sbn-left {
    width: 185px;
    height: 100%;
  }

  .sbn-left-top {
    width: 185px;
    height: 35px;
    line-height: 35px;
    background-color: #1086ce;
    color: #ffffff;
    font-size: 15px;
  }

  .sbn-left-content {
    position: absolute;
    width: 180px;
    top: 35px;
    bottom: 5px;
    font-size: 13px;
    padding-left: 5px;
    overflow: scroll;
  }

  .sbn-right {
    position: absolute;
    left: 187px;
    right: 5px;
    top: 0px;
    bottom: 5px;
    overflow: scroll;
  }

  .sbn-right-top {
    position: absolute;
    width: 100%;
    height: 50px;
  }

  .sbn-right-content {
    position: absolute;
    top: 50px;
    bottom: 0px;
    right: 5px;
    left: 0px;
  }

  #sbn-right-content-table {
    width: 100%;
    border: solid #adaa9c;
    border-width: 1px 0px 0px 1px;
    text-align: center;
    font-size: 15px;
  }

  #sbn-right-content-table th {
    height: 35px;
    font-size: 15px;
    background-color: #1086ce;
    border-right: solid 1px #adaa9c;
    color: white;
    padding: 0 10px 0 10px;
  }

  #sbn-right-content-table tr {
    height: 35px;
  }

  #sbn-right-content-table td {
    border: solid #adaa9c;
    border-width: 0px 1px 1px 0px;
    padding: 0px 5px 0px 5px;
  }

  .goods-active {
    color: rgb(255, 255, 255);
  }

  .goods-active td {
    background-color: rgb(123, 150, 189);
  }

</style>
