<template>
  <div id="special-copy-content" class="layui-layer-wrap" style="display: none;">
    <div class="copy-top">
      <div class="copy-top-search">
        <input class="copy-top-search-input"/>
      </div>
      <div class="copy-top-exit" @click="click_exit_copy()"></div>
    </div>
    <div class="copy-content" style="overflow-x: auto;height: calc( 100% - 50px);">
      <table
        id="FZTABLE"
        cellpadding="0px"
        cellspacing="0px"
        style="width: 250%;height: auto;border: solid #adaa9c;border-width: 1px 0px 0px 1px;"
      >
        <tbody>
        <tr class="copy-tr" style="background-color: rgb(255, 255, 255);">
          <th>发票种类</th>
          <th>发票代码</th>
          <th>发票号码</th>
          <th>开票机号</th>
          <th>购方名称</th>
          <th>购方税号</th>
          <th>购方地址电话</th>
          <th>购方银行账号</th>
          <th>报送状态</th>
          <th>报送日志</th>
          <th>所属月份</th>
          <th>开票日期</th>
          <th>税率</th>
          <th>合计税额</th>
          <th>主要商品名称</th>
          <th>商品税目</th>
          <th>开票人</th>
          <th>收款人</th>
          <th>打印标志</th>
          <th>作废标志</th>
          <th>清单标志</th>
          <th>修复标志</th>
          <th>复核人</th>
          <th>作废日期</th>
        </tr>
        <tr
          class="copy-tr"
          v-for="(item, i) in invoice_zhuan"
          :style="i===selectNum?'background-color: rgb(0, 153, 255);':''"
          :key="i"
          @click="select(i)"
          @dblclick="dbSelectPop(i)"
        >
          <td>
            <input type="text" readonly="readonly" value="专用发票"/>
          </td>
          <td>
            <input type="text" readonly="readonly" :value="item.invoice_code"/>
          </td>
          <td>
            <input type="text" readonly="readonly" :value="item.invoice_Number"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <!-- 购方姓名 -->
          <td>
            <input type="text" readonly="readonly" :value="item.piao_form.customer_name"/>
          </td>
          <td>
            <input type="text" readonly="readonly" :value="item.piao_form.customer_taxcode"/>
          </td>
          <td>
            <input type="text" readonly="readonly" :value="item.piao_form.customer_forth_textarea"/>
          </td>
          <td>
            <input type="text" readonly="readonly" :value="item.piao_form.customer_fifth_textarea"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="报送状态"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="报送日志"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="所属月份"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="2018年12月03日"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="3%"/>
          </td>
          <td>
            <input type="text" readonly="readonly" :value="item.piao_value.shuie"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="详见销货清单"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="详见销货清单"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value="管理员"/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
          <td>
            <input type="text" readonly="readonly" value/>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'invoice-all-pop',
    computed: {
      invoice_zhuan: {
        get () {
          return this.$store.getters.getZhuanInvoice
        }
      }
    },
    data () {
      return {
        selectNum: false,
        layerCount: false
      }
    },
    methods: {
      copy_pop () {
        this.layerCount = layer.open({
          type: 1,
          title: ['发票复制', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: '796px',  //宽高
          content: $('#special-copy-content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('special-copy-content').style.display = 'none'
          }
        })
      },
      select (i) {
        this.selectNum = i
      },
      dbSelectPop (i) {
        this.$emit('dbselect', i)
        layer.close(this.layerCount)
      },
      click_exit_copy () {
        layer.close(this.layerCount)
      }
    }
  }
</script>


<style scoped>
  #special-copy-content {
    width: 796px;
    height: 430px;
    border: solid #cae1f7;
    border-width: 0px 5px 5px 5px;
    display: none;
  }

  .copy-top {
    height: 50px;
    width: 100%;
    background: url("../../../assets/img/special_copy.png") no-repeat center;
  }

  .copy-top-search {
    width: 180px;
    height: 20px;
    line-height: 20px;
    margin: 15px 0px 0px 67px;
    float: left;
  }

  .copy-top-search-input {
    width: 176px;
    height: 15px;
    border: none;
    outline: none;
  }

  .copy-top-exit {
    width: 75px;
    height: 35px;
    border: solid 1px transparent;
    margin: 6px 7px 0px 0px;
    cursor: pointer;
    float: right;
  }

  .copy-top-exit:hover {
    border: solid 1px #0056eb;
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .copy-tr {
    font-size: 14px;
    height: 35px;
  }

  .copy-tr th {
    border: solid #adaa9c;
    border-width: 0px 1px 1px 0px;
    color: #ffffff;
    background-color: #1086ce;
    padding: 0px 2px 0px 2px;
  }

  .copy-tr td {
    border: solid #adaa9c;
    border-width: 0px 1px 1px 0px;
    padding: 0 10px;
  }

  table input {
    background-color: transparent;
    border: 0px;
  }
</style>
