<template>
  <div>
    <div id="special-buyer-red" class="layui-layer-wrap special-buyer-name" style="display: none;">
      <!--左-->
      <div class="sbn-left">
        <div class="sbn-left-top">
          <img src="@/assets/img/ico-menu1.png" style="margin: 7px 2px -5px 5px"/>编码族管理
        </div>
        <div class="sbn-left-content">
          <ul id="file" class="filetree treeview">
            <li class="expandable lastExpandable">
              <div class="hitarea expandable-hitarea lastExpandable-hitarea"></div>
              <span class="folder 000" onclick="sbn_select(this)">全部</span>
              <ul id="kgbmz_sbn"></ul>
            </li>
          </ul>
        </div>
      </div>
      <!--右-->
      <div class="sbn-right">
        <div class="sbn-right-top">
          <div class="right-box"></div>
          <div style="width: 150px;height: 23px;margin-top: 10px;float: left">
            <input style="width: 100%;height: 100%"/>
          </div>
        </div>
        <div class="sbn-right-content">
          <table id="sbn-right-content-table" cellpadding="0px" cellspacing="0px">
            <tbody>
            <tr>
              <th>编码</th>
              <th>名称</th>
              <th>简码</th>
              <th>税号</th>
              <th>地址电话</th>
              <th>银行账号</th>
              <th>邮件地址</th>
              <th>备注</th>
            </tr>
            <tr
              v-for="(item, i) in kehu_form"
              :key="i"
              @click="select_goods2(i)"
              @dblclick="dbSelectKehu(i)"
              :class="i===goodsNumber?'goods-active':''"
            >
              <td>{{item.customer_code}}</td>
              <td>{{item.customer_name}}</td>
              <td>{{item.customer_short_code}}</td>
              <td>{{item.customer_taxcode}}</td>
              <td>{{item.customer_forth_textarea}}</td>
              <td>{{item.customer_fifth_textarea}}</td>
              <td>{{item.customer_email}}</td>
              <td>{{item.remark}}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'special-buyer-red',
    data () {
      return {
        kehuDom: false,
        goodsNumber: 0,
        num: ''
      }
    },
    computed: {
      ...mapState({
        kehu_form: (state) => { //加载客户的信息
          return state.form.kehu_form
        }
      })
    },
    methods: {
      pop_buyer_name () {
        this.kehuDom = layer.open({
          type: 1,
          title: ['客户选择', 'color:#ffffff;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['1100px', '655px'],    //宽、高
          offset: '10px',
          fixed: false,
          content: $(`#special-buyer-red`),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            // document.getElementById('special-buyer-name').style.display = 'none';
          }
        })
      },
      dbSelectKehu (i) { // 选择商品的按钮
        let kehuData = this.kehu_form[i]
        this.$emit('getDbKehu', kehuData)
        layer.close(this.kehuDom)
      },
      select_goods2 (i) { // 选择的时候高亮用的
        this.goodsNumber = i
      }
    }
  }
</script>

