  }

  .sbn-left-top {
    width: 185px;
    height: 35px;
    line-height: 35px;
    background-color: #1086ce;
    color: #ffffff;
    font-size: 15px;
  }

  .sbn-left-content {
    position: absolute;
    width: 180px;
    top: 35px;
    bottom: 5px;
    font-size: 13px;
    padding-left: 5px;
    overflow: scroll;
  }

  .sbn-right {
    position: absolute;
    left: 187px;
    right: 5px;
    top: 0px;
    bottom: 5px;
    overflow: scroll;
  }

  .sbn-right-top {
    position: absolute;
    width: 100%;
    height: 50px;
  }

  .sbn-right-content {
    position: absolute;
    top: 50px;
    bottom: 0px;
    right: 5px;
    left: 0px;
  }

  #sbn-right-content-table {
    width: 100%;
    border: solid #adaa9c;
    border-width: 1px 0px 0px 1px;
    text-align: center;
    font-size: 15px;
  }

  #sbn-right-content-table th {
    height: 35px;
    font-size: 15px;
    background-color: #1086ce;
    border-right: solid 1px #adaa9c;
