<script>
  import buyerRed from './special-buyer-red'
  import staticPop from './static-pop' // 这是一些不关联数据的无关紧要的弹窗
  import goodsRed from './special-goods-red'
  import { convertCurrency, toPoint } from '../../../until/dataHelper'
  import printPop from '@/components/print-pop.vue'

  export default {
    name: 'invoice-red',
    components: {
      buyerRed,
      staticPop,
      goodsRed,
      printPop
    },
    data () {
      return {
        formId: 'red_form',
        red_form: { //专票的核心数据，数据绑定
          customer_name: '',
          customer_taxcode: '',
          isFangwei: '',
          red_table_data: [{
            CCP_sp_name: '', //名称
            CCP_UnitOfMeasurement: '',//规格
            CCP_UnitPrice: '',//单位
            CCP_Specificationmodel: '', //数量
            CCP_taxrate: '16%' // 税率
          }]
        },
        select_goods_table_num: false,
        invoice_number_red: '',
        invoice_code_red: ''
      }
    },
    computed: {
      piao_value: { // 专票的用于计算的数据
        get () {
          let all = {}
          all.jine = 0
          all.shuie = 0
          let value = []
          let goodsTable = this.red_form.red_table_data // 这是商品数据
          for (let i in goodsTable) { //遍历商品
            value[i] = {} // table右面的计算属性
            value[i].num = goodsTable[i].CCP_UnitPrice * goodsTable[i].count ? goodsTable[i].CCP_UnitPrice * goodsTable[i].count : 0 // 右面计算属性金额
            value[i].shuie = (value[i].num * toPoint(goodsTable[i].CCP_taxrate)).toFixed(2) // 税率
            all.jine += value[i].num //合计
            all.shuie += parseFloat(value[i].shuie)
            all.jiashui = (parseFloat(all.jine) + parseFloat(all.shuie)).toFixed(2)
            all.jiashuiBig = convertCurrency(all.jiashui)
            all.table = value
          }
          return all
        }
      }
    },
    methods: {
      click_btn_confirm (data) {
        this.invoice_number_red = data.invoice_number_red
        this.invoice_code_red = data.invoice_code_red
        layer.open({
          type: 1,
          title: ['开具红色增值税专用发票信息表', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['1200px', '500px'],  //宽高
          offset: ['10px', '10px'],
          fixed: false,
          content: $('#pop-red-message-content'),    //内容
          resize: true,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          maxmin: true,
          cancel: function () {
            $('#pop-red-message-content').css('display', 'none')
          }
        })
      },
      pop_static (method) { //所有的不关联数据的静态弹窗方法都在这里
        this.$refs.staticPop[method]()
      },
      getkehu () { //点击小图片导入客户
        this.$refs.buyerRed.pop_buyer_name()
      },
      setKeHu (data) { // 双击客户导入
        this.red_form.customer_name = data.customer_name
        this.red_form.customer_taxcode = data.customer_taxcode
      },
      add_tr () { //添加一行
        this.red_form.red_table_data.push({
          CCP_sp_name: '',
          CCP_UnitOfMeasurement: '',
          CCP_UnitPrice: '',
          CCP_Specificationmodel: '',
          CCP_taxrate: '16%'
        })
      },
      delete_tr () {
        var table = this.red_form.red_table_data
        var num = this.select_goods_table_num
        layer.confirm('删除将不可恢复，确定删除吗？', {
          btn: ['确定', '取消'] //按钮
        }, function () {
          if (num) {
            table.splice(num, 1)
            this.select_goods_table_num = false
          } else {
            table.pop()
          }
          layer.msg('删除成功')
        }, function () {
          layer.msg('未删除')
          return
        })

      },
      selectGoods (i) {
        this.select_goods_table_num = i
      },
      red_chose_goods (i) {
        this.$refs.goods.pop_goods_name(i)
      },
      closeAll () {
        layer.closeAll()
      },
      setGoods (goodsData) { // 获取到选中的商品，然后赋值出来
        let i = this.select_goods_table_num
        let table = this.red_form.red_table_data[i]
        table.CCP_sp_name = goodsData.CCP_sp_name ? goodsData.CCP_sp_name : ''
        table.CCP_UnitOfMeasurement = goodsData.CCP_UnitOfMeasurement ? goodsData.CCP_UnitOfMeasurement : ''
        table.CCP_UnitPrice = goodsData.CCP_UnitPrice ? goodsData.CCP_UnitPrice : ''
        table.CCP_Specificationmodel = goodsData.CCP_Specificationmodel ? goodsData.CCP_Specificationmodel : ''
        table.CCP_taxrate = goodsData.CCP_taxrate ? goodsData.CCP_taxrate : ''
      },
      numberInput (item) {
        if (parseInt(item.count) > 0) {
          layer.msg('数量必须小于等于0')
          item.count = 0
        }
      },
      print () {
        this.$refs.print.showPop(this.setForm)
      },
      setForm () {
        let saveData = {}
        saveData.invoice_number_red = this.invoice_number_red
        saveData.invoice_code_red = this.invoice_code_red
        saveData.piao_form = this.red_form
        saveData.piao_value = this.piao_value
        saveData.shenheStatus = '审核通过'
        saveData.red_water_number = this.$store.state.service.red_water_number
        this.$store.commit('addFormInfo', { formId: this.formId, content: saveData })
        this.$store.commit('add_red_water_number')
        Object.assign(this.$data, this.$options.data())
        this.closeAll()
      }
    }
  }
</script>

