<template>
  <div id="special-printer" class="print"></div>
</template>

<script>
  import { setTimeout } from 'timers'

  export default {
    name: 'print-pop',
    methods: {
      showPop (callBack) {
        let printDom = layer.open({
          type: 1,
          title: ['打印', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: '410px',    //宽高
          content: $('#special-printer'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('special-printer').style.display = 'none'
          }
        })
        setTimeout(() => {
          callBack()
          layer.msg('打印成功')
          layer.close(printDom)
        }, 1000)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .print {
    width: 410px;
    height: 330px;
    border-style: solid;
    border-color: rgb(202, 225, 247);
    border-image: initial;
    border-width: 0px 5px 5px;
    background: url(../assets/img/printer.png);
    display: none;
  }
</style>
