<template>
  <div>
    <div class="indexInvoice-content">

      <div class="indexInvoice-content-query" @click="queryClick"></div>

      <!--发票查询-->
      <div class="indexInvoice-content-repair" @click="pop_repair"></div>
      <!--发票修复-->
      <div class="indexInvoice-content-read" @click="pop_read"></div>
      <!--发票读入-->
      <div class="indexInvoice-content-storage" @click="storageClick"></div>
      <!--库存查询-->
      <div class="indexInvoice-content-write" @click="display_pic"></div>
      <!--发票填开-->
      <div id="indexInvoice-content-write-pic" v-show="picShow">
        <!--发票填开的左边图片-->
        <div class="pic-chose" @click="pop_private"></div>
        <!--专用发票-->
        <div class="pic-chose" @click="pop_common"></div>
        <!--普通发票-->
      </div>
      <router-link to="/kp-invoice-cancel">
        <div class="indexInvoice-content-cancel"></div>
      </router-link>
      <!--发票作废-->
      <div class="indexInvoice-content-message" @click="zuofen_click"></div>
      <!--信息表-->
      <div id="indexInvoice-content-message-pic" v-show="zuofeiShow">
        <!--信息表左边的图片-->
        <div class="pic-message-chose" @click="pop_red_chose"></div>
        <!--红字专用发票-->
      </div>

      <div class="indexInvoice-content-back"></div>
      <!--发票退回-->
    </div>

    <!-- 发票读入 -->
    <div id="pop-invoice-read">
      <div id="invoice-read-confirm" @click="dupiao"></div>
      <div id="invoice-read-cancel" @click="pop_read_cancel"></div>
    </div>

    <!-- 发票读入成功的弹窗 -->
    <div id="pop-read-success" class="layui-layer-wrap">
      <table
        class="pop-read-success-table"
        style="width: 805px;height: 20px;margin-top: 71px;display: inline-block;border: none"
      >
        <tbody>
        <tr>
          <td width="190px">增值税专用发票</td>
          <td width="95px">{{yushe.zhuan_xiane}}</td>
          <td width="95px">{{yushe.zhuan_code}}</td>
          <td width="195px">xx省增值税专用发票</td>
          <td width="80px">{{yushe.zhuan_code_start}}</td>
          <td width="70px">{{yushe.zhuan_number}}</td>
          <td width="80px">{{yushe.month + '-01'}}</td>
        </tr>
        <tr>
          <td width="190px">增值税普通发票</td>
          <td width="95px">{{yushe.po_xiane}}</td>
          <td width="95px">{{yushe.po_code}}</td>
          <td width="195px">xx省增值税普通发票</td>
          <td width="80px">{{yushe.po_code_start}}</td>
          <td width="70px">{{yushe.po_number}}</td>
          <td width="80px">{{yushe.month + '-01'}}</td>
        </tr>
        </tbody>
      </table>
      <div id="read-success-btn" @click="pop_read_cancel"></div>
    </div>

    <!-- 发票报错的窗口 -->
    <div id="pop-read-fail" class="layui-layer-wrap">
      <div style="display:inline-block;margin: 17px 0px 0px 153px">2018-12-01</div>
      <div
        style="width: 75px;height: 30px;margin: 298px 0px 0px 293px;cursor: pointer"
        @click="pop_read_cancel"
      ></div>
    </div>

    <!-- 这是发票号码确认的窗口 -->
    <div id="private-content" class="layui-layer-wrap" style="display: none;">
      <div class="private-classify">
        <input type="text" readonly="readonly" value="专用发票"/>
      </div>
      <div class="private-code">
        <input type="text" readonly :value="$store.state.service.invoice_zhuan_code"/>
      </div>
      <div class="private-number">
        <input type="text" readonly :value="$store.state.service.invoice_zhuan_Number_now"/>
      </div>
      <div class="private-confirm" @click="private_confirm"></div>
      <!--确定-->
      <div class="private-cancel" @click="pop_read_cancel"></div>
      <!--取消-->
    </div>
    <!-- 这是点击普票填写的窗口 -->
    <div id="pop-common-table" class="layui-layer-wrap" style="display: none;">
      <div id="aa">
        <div class="common-table-top-chose" @click="click_q_chose"></div>
        <div class="common-table-top-exit" @click="click_q_exit"></div>
      </div>
      <div
        style="position: absolute;top: 55px;bottom: 5px;left: 5px;right: 5px;background-color: #f7f3f7"
      >
        <table id="common-q-table" cellpadding="0px" cellspacing="0px" style="width: 660px">
          <tbody>
          <tr>
            <th>序号</th>
            <th>发票种类</th>
            <th>类别代码</th>
            <th>起始号码</th>
            <th>发票张数</th>
          </tr>
          <tr>
            <td>1</td>
            <td>增值税普通发票</td>
            <td>{{yushe.po_code}}</td>
            <td id="Front_desk">{{$store.state.service.invoice_po_Number_now}}</td>
            <td id="Front_code">{{parseInt(yushe.po_code_end) - parseInt($store.state.service.invoice_po_Number_now)}}
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- 这是普票的弹窗 -->
    <div id="common-content" class="layui-layer-wrap" style="display: none;">
      <div class="common-classify">
        <input
          type="text"
          style="border:0px; background-color:transparent; color:#F0F; font-size:16px; width:100%; height:100%;"
          readonly="readonly"
          value="普通发票"
        />
      </div>
      <div class="common-code">
        <input
          type="text"
          readonly
          style="border:0px; background-color:transparent; font-size:16px; width:100%; color:#F0F; height:100%;"
          :value="$store.state.service.invoice_po_code"
        />
      </div>
      <div class="common-number">
        <input
          type="text"
          readonly
          id="C_CTFPDM"
          style="border:0px; background-color:transparent; font-size:16px; color:#F0F; width:100%; height:100%;"
          :value="$store.state.service.invoice_po_Number_now"
        />
      </div>
      <div class="common-confirm" @click="pupiao_confirm_click"></div>
      <!--确定-->
      <div class="common-cancel" @click="pop_read_cancel"></div>
      <!--取消-->
    </div>

    <!-- 这是发票修复的窗口 -->
    <div id="repair-content" class="layui-layer-wrap" style="display: none;">
      <div class="repair-text">发票修复</div>
      <div class="repair-year">
        <select>
          <option>{{$store.state.service.serviceData.month.substr(0, 4)}}年</option>
        </select>
      </div>
      <div class="repair-month">
        <select>
          <option>{{$store.state.service.serviceData.month.substr(-2, 2)}}月份</option>
        </select>
      </div>
      <div class="repair-confirm" @click="pop_repair_success"></div>
      <div class="repair-cancel" @click="pop_read_cancel"></div>
    </div>

    <!-- 这是发票修复成功的窗口 -->

    <div id="repair-success" class="layui-layer-wrap" style="display: none;">
      <div class="repair-success-btn" @click="pop_read_cancel"></div>
    </div>

    <!-- 这是红色信息表选择的窗口 -->

    <div id="pop-red-chose-content" class="layui-layer-wrap" style="display: none;">
      <!--购买方申请-->
      <div class="title1">
        <input
          type="radio"
          name="title1"
          style="margin-left: 40px"
          value="title1"
          checked="checked"
          @click="radioSelect('goumai')"
        />
      </div>
      <!--已抵扣-->
      <div class="title1-1">
        <input
          type="radio"
          name="title1-1"
          style="margin-left: 70px"
          value="title1-have"
          checked="checked"
          disabled
        />
      </div>
      <!--未抵扣-->
      <div class="title1-1">
        <input type="radio" name="title1-1" style="margin-left: 70px" value="title1-none" disabled/>
      </div>
      <!--销售方申请-->
      <div class="title2">
        <input
          type="radio"
          name="title1"
          style="margin-left: 40px"
          value="title2"
          @click="radioSelect('xiaoshou')"
        />
      </div>
      <!--发票种类-->
      <div class="chose-classify">
        <select>
          <option>增值税专用发票</option>
        </select>
      </div>
      <!--发票代码、发票号码(数字)-->
      <div class="chose-code-num">
        <input
          type="text"
          class="chose-code-input"
          id="chose-code-input"
          :disabled="!isXiaoshou"
          v-model="invoice_code_red"
        />
        <input
          type="text"
          class="chose-num-input"
          id="chose-num-input"
          :disabled="!isXiaoshou"
          v-model="invoice_number_red"
        />
      </div>
      <!--下一步按钮、确定按钮-->
      <div class="chose-btn">
        <input
          type="button"
          class="chose-btn-next"
          value="下一步"
          id="chose-btn-next"
          disabled="isXiaoshou"
        />
        <input
          type="button"
          class="chose-btn-confirm"
          value="确定"
          id="chose-btn-confirm"
          :style="isXiaoshou?'':'background-color: rgb(198, 195, 189);cursor:default'"
          @click="confirmRed"
        />
        <div
          style=" width: 75px;height: 30px;float: right;margin-right:10px;cursor:pointer"
          @click="pop_read_cancel"
        ></div>
      </div>
    </div>

    <!-- 这是开始想写票的窗口 -->
    <zhuanpiao v-if="zhuanpiao_show"></zhuanpiao>
    <pupiao v-if="pupiao_show"></pupiao>
    <red ref="redPop"></red>
  </div>
</template>

<script>
  import zhuanpiao from './components/zhuanpiao'
  import pupiao from './components/pupiao'
  import red from './components/invoice-red'

  export default {
    components: {
      zhuanpiao,
      pupiao,
      red
    },
    data () {
      return {
        pupiao_show: false,
        zhuanpiao_show: false,
        redpiao_show: false,
        picShow: false,
        zuofeiShow: false,
        isXiaoshou: false,
        invoice_code_red: '',
        invoice_number_red: '',
        errorPop: false
      }
    },
    computed: {
      yushe () {
        let a = this.$store.state.service.serviceData
        a.po_number = parseInt(a.po_code_end) - parseInt(a.po_code_start)
        a.zhuan_number = parseInt(a.zhuan_code_end) - parseInt(a.zhuan_code_start)
        return a
      }
    },
    methods: {
      pop_read () {
        layer.open({
          type: 1,
          title: ['确认', 'color:#ffffff;background-color:#315573;border-bottom:solid 1px #315573;'],
          area: ['400px', '210px'],  //宽高
          content: $('#pop-invoice-read'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            $('#pop-invoice-read').css('display', 'none')
          }
        })
      },
      pop_read_confirm () {
      },
      pop_read_cancel () { // 全部关闭
        layer.closeAll()
        this.picShow = false
        this.zuofeiShow = false
      },
      display_pic () { // 显示发票填开左边选择框图片
        this.picShow = this.picShow ? false : true
      },
      zuofen_click () {
        this.zuofeiShow = this.zuofeiShow ? false : true
      },
      pop_private () { // 点击增值税专用发票
        if (!this.$store.state.service.invoice_zhuan_code) {
          layer.msg('请先进行发票读入')
          return
        }
        this.zhuanpiao_show = true
        this.pupiao_show = false
        this.redpiao_show = false
        layer.open({
          type: 1,
          title: ['发票号码确认', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['424px', '311px'],  //宽高
          content: $('#private-content'),    //内容
          resize: false,    //不允许拉伸
          shade: 0,    //遮罩阴影
          cancel: function () {
            document.getElementById('private-content').style.display = 'none'
          }
        })
      },
      pop_common () { // 点击增值税普通发票
        if (!this.$store.state.service.invoice_po_code) {
          layer.msg('请先进行发票读入')
          return
        }
        this.zhuanpiao_show = true
        this.pupiao_show = false
        this.redpiao_show = false
        layer.open({
          type: 1,
          title: ['发票卷选择', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['706px', '505px'],  //宽高
          content: $('#pop-common-table'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('pop-common-table').style.display = 'none'
          }
        })
      },
      click_q_chose () { // 点击普票的选择按钮
        layer.open({
          type: 1,
          title: ['发票号码确认', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['424px', '311px'],  //宽高
          content: $('#common-content'),    //内容
          resize: false,    //不允许拉伸
          shade: 0,    //遮罩阴影
          cancel: function () {
            document.getElementById('private-content').style.display = 'none'
          }
        })
      },
      private_confirm () { // 点击公用的那个确定按钮
        layer.closeAll()
        layer.open({
          type: 1,
          title: ['开具增值税专用发票', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['1066px', '742px'],
          offset: '10px',
          fixed: false,
          content: $('#special-content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          success: function () {
          },
          cancel: function () {
          }
        })
      },
      click_q_exit () { // 点击普票的退出按钮
        this.pop_read_cancel()
      },
      pop_repair () {
        layer.open({
          type: 1,
          title: ['发票修复', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['360px'],  //宽高
          content: $('#repair-content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('repair-content').style.display = 'none'
          }
        })
      },
      pop_repair_success () {
        layer.closeAll()
        layer.open({
          type: 1,
          title: ['确认', 'color:#ffffff;background-color:#315573;border-bottom:solid 1px #315573;'],
          area: ['388px', '208px'],  //宽高
          content: $('#repair-success'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('repair-success').style.display = 'none'
          }
        })
      },
      pop_red_chose () { // 红字增值税
        layer.open({
          type: 1,
          title: ['红色增值税专用发票信息表信息选择', 'color:#ffffff;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['450px'],  //宽高
          content: $('#pop-red-chose-content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('pop-red-chose-content').style.display = 'none'
          }
        })
      },
      pupiao_confirm_click () {
        this.zhuanpiao_show = false
        this.pupiao_show = true
        this.redpiao_show = false
        layer.open({
          type: 1,
          title: ['开具增值税普通发票', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['1056px', '755px'],  //宽高
          offset: '10px',
          fixed: false,
          //content: $('#common-write-content'),    //普通发票内容
          content: $('#pupiao-content'),    //普通发票内容（直接取专用发票内容）
          resize: false,    //不允许拉伸
          shade: 0,    //遮罩阴影
          cancel: function () {
            layer.closeAll()
            document.getElementById('pupiao-content').style.display = 'none'
          }
        })
      },
      dupiao_error () {
        this.errorPop = layer.open({
          type: 1,
          title: ['错误信息', 'color:#ffffff;background-color:#315573;border:solid 1px #315573;'],
          area: ['394px'],  //宽高
          content: $('#pop-read-fail'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('pop-read-fail').style.display = 'none'
          }
        })

      },
      radioSelect (type) {
        if (type === 'goumai') {
          this.isXiaoshou = false
        } else {
          this.isXiaoshou = true
        }
      },
      confirmRed () {
        if (!this.isXiaoshou) return
        if (this.invoice_code_red.length < 10) {
          layer.msg('发票代码不能小于十位')
        } else {
          let data = {}
          data.invoice_number_red = this.invoice_number_red
          data.invoice_code_red = this.invoice_code_red
          this.$nextTick(() => {
            this.$refs.redPop.click_btn_confirm(data)
          })
          // this.$refs.redPop.click_btn_confirm(data)
        }
      },
      dupiao () {
        if (this.$store.state.service.invoice_zhuan_code && this.$store.state.service.invoice_po_code) {
          this.dupiao_error()
        } else {
          this.$store.commit('getInvioce')
          layer.open({
            type: 1,
            title: ['从金税设备读入新购发票', 'color:#ffffff;background-color:#b2d3f2;border:solid 1px #232b32;'],
            area: ['815px', '290px'],  //宽高
            content: $('#pop-read-success'),    //内容
            resize: false,    //不允许拉伸
            shade: [0.1, '#3b6ea5'],    //遮罩阴影
            cancel: function () {
              document.getElementById('pop-read-success').style.display = 'none'
            }
          })
          // this.$store.commit('getInvioce')
        }
      },
      close_read_success () {
        console.log(1)
      },
      storageClick () { // 库存查询
        if (!this.$store.state.service.invoice_zhuan_code) {
          layer.msg('请先进行发票读入')
          return
        } else {
          this.$router.push('/kp-invoice-storage')
        }
      },
      queryClick () {
        if (!this.$store.state.form.invoice_form.length) {
          layer.msg('无可查询数据')
          return
        } else {
          this.$router.push('/kp-invoice-query')
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .indexInvoice-content {
    position: absolute;
    height: 528px;
    width: 800px;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background: url("../../assets/img/con_invok.png");
  }

  .indexInvoice-content-query {
    width: 65px;
    height: 75px;
    margin: 128px 0 0 545px;
    cursor: pointer;
    float: left;
    border-radius: 3px;
  }

  .indexInvoice-content-query:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .indexInvoice-content-repair {
    width: 65px;
    height: 75px;
    margin: 128px 0px 0px 70px;
    float: left;
    cursor: pointer;
    border-radius: 3px;
  }

  .indexInvoice-content-repair:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .indexInvoice-content-read {
    clear: left;
    width: 65px;
    height: 75px;
    margin: 25px 0 0 50px;
    float: left;
    cursor: pointer;
    border-radius: 3px;
  }

  .indexInvoice-content-read:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .indexInvoice-content-storage {
    width: 65px;
    height: 75px;
    margin: 25px 0 0 102px;
    float: left;
    cursor: pointer;
  }

  .indexInvoice-content-storage:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .indexInvoice-content-write {
    width: 65px;
    height: 75px;
    margin: 25px 0px 0px 114px;
    float: left;
    cursor: pointer;
  }

  .indexInvoice-content-write:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  #indexInvoice-content-write-pic {
    position: absolute;
    width: 240px;
    height: 48px;
    margin: 230px 0px 0px 156px;
    background: url("../../assets/img/write.png");
    border: solid 1px #7e7e7e;
  }

  .pic-chose {
    width: 238px;
    height: 24px;
    border: solid 1px transparent;
    cursor: pointer;
  }

  .pic-chose:hover {
    border: solid 1px #5094d1;
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .indexInvoice-content-cancel {
    clear: left;
    width: 65px;
    height: 75px;
    margin: 22px 0px 0px 544px;
    float: left;
    cursor: pointer;
  }

  .indexInvoice-content-cancel:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  .indexInvoice-content-message {
    width: 65px;
    height: 75px;
    margin: 22px 0px 0px 70px;
    float: left;
    cursor: pointer;
  }

  .indexInvoice-content-message:hover {
    box-shadow: #b9e2ff 0 0 40px inset;
  }

  #indexInvoice-content-message-pic {
    position: absolute;
    width: 323px;
    height: 48px;
    background: url("../../assets/img/red-chose.png");
    margin: 325px 0px 0px 357px;
    /* display: none; */
  }

  .pic-message-chose {
    width: 323px;
    height: 22px;
    border: solid 1px transparent;
