          class="dis-row-input"
          v-model.number="zhekou.zhekouhang"
          @input="zhekouhangInput"
          id="dis_row_input_row"
        />
      </div>
      <div class="dis-sum">
        <!--商品金额-->
        <input id="ZKSPJE" readonly type="text" class="dis-row-input" v-model="zhekou.shangpinjine"/>
      </div>
      <div class="dis-dis-rate">
        <!--折扣率-->
        <input
          id="dis_rate"
          type="number"
          v-model.number="zhekou.zhekoulv"
          style="width:155px;left: -2px;position: relative;"
          @input="zhekouhangInput"
        />
      </div>
      <div class="dis-dis-num">
        <!--折扣金额-->
        <input type="text" id="ZKZKJE" class="dis-row-input" v-model="zhekou.zhekoujine"/>
      </div>
      <div class="dis-btn">
        <!--按钮-->
        <div class="dis-btn-confirm">
          <button
            id="dis_row_QD"
            style="background-color: rgb(24, 121, 222);"
            class="confirm-btn"
            @click="zhekouConfirm"
          >确定
          </button>
        </div>
        <div class="dis-btn-cancel" @click="layerClose(zhekouDom)"></div>
      </div>
    </div>
    <static-pop ref="staticPop"></static-pop>
    <!-- 这是打印的弹窗 -->
    <print-pop ref="print"></print-pop>
    <!-- 这是复制的弹窗 -->
    <invoicePop ref="copy" @dbselect="dbSelect"></invoicePop>
    <goodsPop ref="goods" @getDbGoods="setGoods"></goodsPop>
    <!-- 这是客户的弹窗 -->
    <buyerPop ref="buyer" @getDbKehu="setKehu"></buyerPop>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  // import { parse } from 'path';
  import { convertCurrency, toPoint } from '../../../until/dataHelper'
  import printPop from '@/components/print-pop.vue' // 这是打印的组件
  import invoicePop from './invoice-all-pop2' // 这是所有的专票，用于复制过来用的
  import staticPop from './static-pop' // 这是一些不关联数据的无关紧要的弹窗
  import goodsPop from './special-goods-name2' // 这是选择商品的弹窗
  import buyerPop from './special-buyer-name2' // 这是选择客户的弹窗
  export default {
    components: {
      printPop,
      invoicePop,
      staticPop,
      goodsPop,
      buyerPop
    },
    data () {
      return {
        formId: 'invoice_form',
        daoruShow: false,
        kehuShow: false,
        hongziShow: false,
        piao_form: { //专票的核心数据，数据绑定
          customer_fifth_textarea: '',
          customer_forth_textarea: '',
          customer_name: '',
          customer_taxcode: '',
          fu_he_ren: '',
          shou_kuan_ren: '',
          invoice_table_data: [{
            CCP_sp_name: '',
            CCP_UnitOfMeasurement: '',
            CCP_UnitPrice: '',
            CCP_Specificationmodel: '',
            CCP_taxrate: '16%',
            isZhekou: false,
            isHaveZhekou: false
          }]
        },
        zhekou: {
          zhekouhang: 0,
          shangpinjine: 0,
          zhekoujine: 0,
          zhekoulv: 0
        },
        zhekouDom: 0,
        select_goods_table_num: false, // 选中的商品的index
        select_goods_num: 0, // 用于控制点击小图标赋值的数儿
        price: false,
        isQingdan: false,
        qingdanDom: false
      }
    },
    computed: {
      ...mapState({
        invoice_zhuan_code: (state) => {
          return state.service.invoice_po_code
        },
        invoice_zhuan_Number: (state) => {
          return state.service.invoice_po_Number_now
        }
      }),
      piao_value: { // 专票的用于计算的数据
        get () {
          let all = {}
          all.jine = 0
          all.shuie = 0
          let value = []
          let goodsTable = this.piao_form.invoice_table_data // 这是商品数据
          for (let i in goodsTable) { //遍历商品
            value[i] = {} // table右面的计算属性
            if (goodsTable[i].isZhekou) { // 如果这是一条折扣行
              value[i].num = goodsTable[i].jine
              value[i].shuie = (value[i].num * toPoint(goodsTable[i].CCP_taxrate)).toFixed(2)
            } else { // 若果不是折扣行
              value[i].num = goodsTable[i].CCP_UnitPrice * goodsTable[i].count ? goodsTable[i].CCP_UnitPrice * goodsTable[i].count : 0 // 右面计算属性金额
              value[i].shuie = (value[i].num * toPoint(goodsTable[i].CCP_taxrate)).toFixed(2) // 税率
            }
            all.jine += value[i].num //合计
            all.shuie += parseFloat(value[i].shuie)
            all.jiashui = (parseFloat(all.jine) + parseFloat(all.shuie)).toFixed(2)
            all.jiashuiBig = convertCurrency(all.jiashui)
            all.table = value
          }
          all.jine = all.jine.toFixed(2)
          return all
        }
      }
    },
    methods: {
      display (box) {
        this[box] = true
      },
      pop_read_cancel () { // 全部关闭
        layer.closeAll()
      },
      hidden (box) {
        this[box] = false
      },
      add_tr () { //添加一行
        if (this.piao_form.invoice_table_data.length >= 8 && !this.isQingdan) {
          layer.msg('已达到最大行数')
          return
        }
        this.piao_form.invoice_table_data.push({
          CCP_sp_name: '',
          CCP_UnitOfMeasurement: '',
          CCP_UnitPrice: '',
          CCP_Specificationmodel: '',
          CCP_taxrate: '16%',
          isZhekou: false,
          isHaveZhekou: false
        })
      },
      delete_tr () { //删除一行
        var table = this.piao_form.invoice_table_data
        var num = this.select_goods_table_num
        if (table.length === 0) {
          return
        }
        if (num) {
          if (table[num].isHaveZhekou) {
            layer.msg('拥有折扣的不能删除，请先删除折扣')
            return
          }
          if (table[num].isZhekou) {
            layer.confirm('这是折扣行，确定删除此折扣嘛', {
              btn: ['确定', '取消'] //按钮
            }, function () {
              table[num - 1].isHaveZhekou = false
              table.splice(num, 1)
              layer.msg('删除成功')
              this.select_goods_table_num = false
            }, function () {
              layer.msg('未删除')
              return
            })
          } else {
            table.splice(num, 1)
            this.select_goods_table_num = false
          }
        } else {
          if (table[table.length - 1].isZhekou) {
            layer.confirm('这是折扣行，确定删除此折扣嘛', {
              btn: ['确定', '取消'] //按钮
            }, function () {
              table[table.length - 1 - 1].isHaveZhekou = false
              table.splice(table.length - 1, 1)
              layer.msg('删除成功')
            }, function () {
              layer.msg('未删除')
              return
            })
          } else {
            table.pop()
          }
        }
      },
      pop_list () { //清单
        // this.piao_form.invoice_table_data = []
        this.isQingdan = true
        this.qingdanDom = layer.open({
          type: 1,
          title: ['清单信息', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['824px', '525px'],  //宽高
          content: $('#special-list-content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('special-list-content').style.display = 'none'
          }
        })
      },
      pop_dis () { // 折扣的按钮
        this.zhekouDom = layer.open({
          type: 1,
          title: ['添加折扣行', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['360px'],  //宽高
          content: $('#special-dis-content2'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('special-dis-content2').style.display = 'none'
          }
        })
      },
      click_price () { // 点击价格
        this.price = this.price ? false : true
      },
      pop_buyer_name () { //选择客户的弹窗
        this.$refs.buyer.pop_buyer_name(2)
      },
      qingdanConfirm () { // 清单确认
        layer.close(this.qingdanDom)
      },
      qingdanToFapiao () {
        if (this.piao_form.invoice_table_data.length >= 8) {
          layer.msg('超出8行没法转发票哦')
          return
        }
        this.isQingdan = false
        layer.close(this.qingdanDom)
      },
      layerClose (index) {
        layer.close(index)
      },
      selectGoods (i) {
        this.select_goods_table_num = i
      },
      zhekouhangInput () { //折扣的输入框判断
        var _this = this
        let zhekouhang = this.zhekou.zhekouhang
        let selectNum = this.select_goods_table_num
        _this.zhekou.shangpinjine = 0
        if (!selectNum && selectNum !== 0) {
          layer.msg('请先选中一条数据')
          return
        }
        if (zhekouhang > selectNum + 1) {
          layer.msg('超出长度了')
          return
        }
        for (let i = selectNum; i > selectNum - zhekouhang; i--) { // 从下往上遍历，看看有没有打折的
          if (_this.piao_form.invoice_table_data[i].isZhekou || _this.piao_form.invoice_table_data[i].isHaveZhekou) { //如果是打折行
            layer.msg('打过折扣了')
            return
          } else if (!_this.piao_value.table[i].num) {
            layer.msg('表没填全，没有金额无法打折')
            return
          } else {
            _this.zhekou.shangpinjine += _this.piao_value.table[i].num
            _this.zhekou.zhekoujine = (_this.zhekou.shangpinjine * this.zhekou.zhekoulv * 0.01).toFixed(2)
          }
        }
      },
      zhekouConfirm () { // 折扣的确定提交按钮
        if (!this.zhekou.shangpinjine) {
          layer.msg('没选择折扣的内容，无法保存')
          return
        }
        var _this = this
        let zhekouhang = this.zhekou.zhekouhang // 折扣行的input
        let selectNum = this.select_goods_table_num // 目前正在选择的那个行的index下标
        if (selectNum - zhekouhang + this.piao_form.invoice_table_data.length > 8 && !this.isQingdan) {
          layer.msg('折扣超出最大数量了，请移步到清单操作')
          return
        }
        for (let i = selectNum; i > selectNum - zhekouhang; i--) { //从下面开始循环，插入折扣行
          _this.piao_form.invoice_table_data[i].isHaveZhekou = true // 先让他变成有折扣的行，变得不能删了
          _this.piao_form.invoice_table_data.splice(i + 1, 0, { // 循环插入
            isZhekou: true, // 变成折扣行
            CCP_taxrate: _this.piao_form.invoice_table_data[i].CCP_taxrate, //折扣行的税率等于他的商品
            jine: (_this.piao_value.jine * 1 * _this.zhekou.zhekoulv * 0.01).toFixed(2) * (-1) // 折扣行的金额，负的
          })
        }
        this.zhekou = this.$options.data().zhekou
        this.layerClose(this.zhekouDom)
      },
      pop_print () { // 打印
        this.$refs.print.showPop(this.saveInvoice)
      },
      saveInvoice () { // 打印的回调
        let saveData = {}
        saveData.invoice_Number = this.invoice_zhuan_Number
        saveData.invoice_code = this.invoice_zhuan_code
        saveData.piao_form = this.piao_form
        saveData.piao_value = this.piao_value
        saveData.isQingdan = this.isQingdan
        saveData.type = 2
        this.$store.commit('addFormInfo', { formId: this.formId, content: saveData })
        this.$store.commit('addInvoicePoNum')
        Object.assign(this.$data, this.$options.data())
        layer.closeAll()
      },
      pop_copy () { // 复制的点击方法
        this.$refs.copy.copy_pop()
      },
      dbSelect (i) { // 复制的子组件双击传过来的方法
        let data = { ...this.$store.state.form.piao_form[i].piao_form }
        this.piao_form = data
      },
      pop_static (method) { //所有的不关联数据的静态弹窗方法都在这里
        this.$refs.staticPop[method]()
      },
      pop_goods_name (i) { // 点击选商品的按钮
        this.select_goods_num = i
        this.$refs.goods.pop_goods_name(i)
      },
      setGoods (goodsData) { // 获取到选中的商品，然后赋值出来
        let i = this.select_goods_num
        this.piao_form.invoice_table_data[i].CCP_sp_name = goodsData.CCP_sp_name ? goodsData.CCP_sp_name : ''
        this.piao_form.invoice_table_data[i].CCP_UnitOfMeasurement = goodsData.CCP_UnitOfMeasurement ? goodsData.CCP_UnitOfMeasurement : ''
        this.piao_form.invoice_table_data[i].CCP_UnitPrice = goodsData.CCP_UnitPrice ? goodsData.CCP_UnitPrice : ''
        this.piao_form.invoice_table_data[i].CCP_Specificationmodel = goodsData.CCP_Specificationmodel ? goodsData.CCP_Specificationmodel : ''
        this.piao_form.invoice_table_data[i].CCP_taxrate = goodsData.CCP_taxrate ? goodsData.CCP_taxrate : ''
      },
      setKehu (kehuData) {
        this.piao_form.customer_name = kehuData.customer_name ? kehuData.customer_name : ''
        this.piao_form.customer_taxcode = kehuData.customer_taxcode ? kehuData.customer_taxcode : ''
        this.piao_form.customer_forth_textarea = kehuData.customer_forth_textarea ? kehuData.customer_forth_textarea : ''
        this.piao_form.customer_fifth_textarea = kehuData.customer_fifth_textarea ? kehuData.customer_fifth_textarea : ''
      },
      pop_red_open () {
        console.log(1)
      }
    }
  }
</script>

