<script>
  export default {
    data () {
      return {
        pop_red_chose_message_index: false,
        download_index: false,
        selectNum: false,
        invoice_piao: [],
        pop_red_open_index: false,
        first_info_code: '',
        second_info_code: '',
        direct: 1,
        invoice_code_red: '',
        invoice_number_red: '',
        messageData: {
          piao_form: {}
        }
      }
    },
    computed: {
      month: {
        get () {
          let a = this.$store.state.service.serviceData.kaipiao_time.slice(5, 7)
          return a
        }
      }
    },
    methods: {
      pop_red_chose_message () {
        this.index_pop_red_chose_message_index = layer.open({
          type: 1,
          title: ['信息表选择', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: '715px',  //宽高
          content: $('#red_chose_message_content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('red_chose_message_content').style.display = 'none'
          }
        })
      },
      download () {
        this.download_index = layer.open({
          type: 1,
          title: ['红字发票(逾期)信息表审核结果下载条件设置', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: '366px',  //宽高
          content: $('#red_download_set'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('red_download_set').style.display = 'none'
          }
        })
      },
      click_download_cancel () {
        layer.close(this.download_index)
      },
      click_download_confirm () {
        let invoiceData = this.$store.getters.getUploadRedInvoice
        this.invoice_piao = invoiceData
        layer.close(this.download_index)
      },
      select (i) {
        this.selectNum = i
      },
      dbSelectPop (item) {
        this.$emit('dbSelectInfo', item)
        layer.close(this.index_pop_red_chose_message_index)
      },
      pop_red_open () {
        this.pop_red_open_index = layer.open({
          type: 1,
          title: ['销项正数发票代码号码填写、确认', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: '598px',  //宽高
          content: $('#red-direct1'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('red-direct1').style.display = 'none'
          }
        })
      },
      closeDirect () {
        layer.close(this.pop_red_open_index)
      },
      redDirect1Next () {
        console.log(this.$store.getters.isRedMessageIn(this.first_info_code))
        if (this.first_info_code !== this.second_info_code) {
          layer.msg('两次输入的不一致')
          return
        } else if (!this.$store.getters.isRedMessageIn(this.first_info_code)) {
          layer.msg('未查询到此信息编号')
          return
        } else {
          this.direct = 2
          layer.close(this.pop_red_open_index)
          this.pop_red_open_index = layer.open({
            type: 1,
            title: ['销项正数发票代码号码填写、确认', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
            area: '598px',  //宽高
            content: $('#red-direct2'),    //内容
            resize: false,    //不允许拉伸
            shade: [0.1, '#3b6ea5'],    //遮罩阴影
            cancel: function () {
              document.getElementById('red-direct1').style.display = 'none'
            }
          })
        }
      },
      redDirect2Next () {
        let messageData = this.$store.getters.isDiffRedMessageAndWater(this.first_info_code, this.invoice_code_red, this.invoice_number_red)
        if (!messageData) {
          layer.msg('红字信息表编号和代码不符')
        } else {
          this.messageData = messageData
          this.direct = 3
          layer.close(this.pop_red_open_index)
          this.pop_red_open_index = layer.open({
            type: 1,
            title: ['销项正数发票代码号码填写、确认', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
            area: '598px',  //宽高
            content: $('#red-direct3'),    //内容
            resize: false,    //不允许拉伸
            shade: [0.1, '#3b6ea5'],    //遮罩阴影
            cancel: function () {
              document.getElementById('red-direct1').style.display = 'none'
            }
          })
        }
      },
      redDirect3Next () {
        this.$emit('SelectInfo', this.messageData)
        this.closeDirect()
      }
    }
  }
</script>

